//import logo from './logo.svg';
import './App.css';
import { Component } from 'react';
import Flat from './components/flat';
import Marker from './components/marker';
import GoogleMapReact from 'google-map-react';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      flats: [],
      selectedFlat: null,
      search: ""
    }
  }

  componentDidMount(){
    const url = "https://raw.githubusercontent.com/lewagon/flats-boilerplate/master/flats.json";
    fetch(url)
    .then (response => response.json())
    .then((data) => {
      this.setState({
       flats: data,
       selectedFlat: data[0]
      });
    })
  }
   
  selectedFlat = (flat) => {
    this.setState({
      selectedFlat: flat
    })
  }

  handleSearch = (event) => {
    this.setState({
      search: event.target.value,
      flats: this.state.flats.filter((flat) => new RegExp(event.target.value, "i").exec(flat.name))
    });
  }


  render() {
    let center = {
    lat: 48.8566,
    lng: 2.3522,
    }

    if (this.state.selectedFlat) {
      center = {
        lat: this.state.selectedFlat.lat,
        lng: this.state.selectedFlat.lng
      }
    }

    return(
    <div className = "app">
      <div className = "main">
        <div className = "search">
          <input
          type = "text"
          placeholder = "Search..."
          value = {this.state.search}
          onChange = {this.handleSearch}
          />
         </div>
         <div className = "flats">
          {this.state.flats.map((flat) => {
            return <Flat key = {flat.name} flat={flat} selectedFlat = {this.selectFlat}/>
          })}
         </div>
        </div>
       <div className = "map">
       <div style={{height: '100vh', width: '500px'}}>
            <GoogleMapReact
                className="container karta"
                bootstrapURLKeys={{
                    key:'AIzaSyC14bEW29qdnztPzM-XekTp2lpX0mNdWNc',
                    language: 'en',
                }}
                
                center={center}
                zoom={11}
                
            >
              {this.state.flats.map((flat) => {
            return <Marker
             key = {flat.name}
             lat = {flat.lat} 
             lng = {flat.lng}
             text = {flat.price}
             selected = {flat === this.state.selectedFlat} />
          })}
            </GoogleMapReact>
        </div> 
      </div>
    </div>
    )
  }
}
export default App;
